<template>
  <div class="item-swiper">
    <swiper :options="swiperOptions" class="ms-swiper">
      <swiper-slide v-for="(item, index) in product" :key="index">
        <img :src="item.img" alt="" />
      </swiper-slide>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination" slot="pagination"></div>

      <!-- 如果需要导航按钮 -->
      <!-- <div class="swiper-button-prev" slot="button-prev"></div> -->
      <!-- <div class="swiper-button-next" slot="button-next"></div> -->
    </swiper>
  </div>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";

export default {
  name: "",
  props: {
    product: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      swiperOptions: {
        // autoplay: true,
        // 分页
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
        // 导航
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      },
    };
  },
  components: {
    swiper,
    swiperSlide,
  },
  mounted() {
    console.log(this.product);
  },
};
</script>

<style lang='scss'>
.item-swiper {
  position: fixed;
  bottom: 105px;
  right: 105px;
  width: 544px;
  height: 657px;
  border-radius: 20px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
  }
  .swiper-pagination {
    bottom: 36px;
  }
  .swiper-button-prev {
    left: 45px;
    top: 58%;
    background-size: 15px 44px;
  }
  .swiper-button-next {
    right: 52px;
    top: 58%;
    background-size: 15px 44px;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets {
    left: -6px;
  }
}
</style>