import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Index from "../views/Index.vue"
import X2 from "../views/X2.vue"
import G3 from "../views/G3.vue"
import Two from "../views/Two.vue"
import One from "../views/One.vue"

Vue.use(VueRouter)


const router = new VueRouter({
  // routes: [
  //   { path: "/", name: Home, component: Index, redirect: "/index" },
  //   { path: "/x2", name: x2, component: X2 },
  // ],
  routes: [{
    path: "/",
    name: "home",
    component: Home,
    redirect: "/index",
    children: [{
      path: "index",
      name: "index",
      component: Index
    }, {
      path: "x2",
      name: "x2",
      component: X2,
    }, {
      path: "g3",
      name: "g3",
      component: G3,
    }, {
      path: "two",
      name: "two",
      component: Two,
    }, {
      path: "one",
      name: "one",
      component: One,
    }]
  },]
})

export default router
