<template>
  <div class="index">
    <div class="video" v-show="isVideo">
      <div class="title">
        <p>未来先生</p>
        <p>把原始森林的好空气带回家</p>
        <p>除霾 • 除醛 • 除菌 • 送新风 • 增负氧</p>
      </div>
      <video src="@/assets/video/cloud.mp4" autoplay loop></video>
    </div>
    <div class="info" slot="info">
      <div class="logo"><img src="@/assets/img/LOGO.png" alt="" /></div>
      <div class="slogn">— 环境健康AIOT —</div>
      <div class="product">
        <div class="x2" @click="btnClick(0)"></div>
        <div class="g3" @click="btnClick(1)"></div>
        <div class="two" @click="btnClick(2)"></div>
        <div class="one" @click="btnClick(3)"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      isVideo: true,
      isShow: true,
    };
  },
  methods: {
    btnClick(num) {
      switch (num) {
        case (num = 0):
          setTimeout(() => {
            this.$router.push("/x2");
            this.isVideo = false;
          }, 500);
          break;
        case (num = 1):
          setTimeout(() => {
            this.$router.push("/g3");
            this.isVideo = false;
          }, 500);
          break;
        case (num = 2):
          setTimeout(() => {
            this.$router.push("/two");
            this.isVideo = false;
          }, 500);
          break;
        case (num = 3):
          setTimeout(() => {
            this.$router.push("/one");
            this.isVideo = false;
          }, 500);
          break;
      }
    },
  },
};
</script>

<style lang='scss'>
.index {
  position: relative;
  background-color: pink;

  .video {
    position: absolute;
    width: 1230px;
    height: 1080px;
    overflow: hidden;
    z-index: 1;
    background-color: #000;
    .title {
      position: absolute;
      // top: 150px;
      top: 300px;
      left: 340px;
      color: #fff;
      opacity: 0.7;
      text-shadow: 0 3px 5px rgba($color: #000, $alpha: 0.5);
      z-index: 3;
      text-align: center;
      & p:nth-child(1) {
        font-size: 56px;
        letter-spacing: 10px;
      }
      & p:nth-child(2) {
        font-size: 36px;
        letter-spacing: 10px;
        margin-top: 5px;
      }
      & p:nth-child(3) {
        font-size: 22px;
        margin-top: 10px;
      }
    }
    video {
      height: 100%;
      // 对图片/视频进行剪切，保留原始比例
      object-fit: cover;
      outline: none;
    }
  }
  .info {
    .logo {
      width: 233px;
      height: 157px;
      margin: 0 auto;
      margin-top: 90px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .slogn {
      margin-top: 50px;
      font-size: 28px;
      color: #666;
    }
    .product {
      width: 344px;
      height: 429px;
      margin: 0 auto;
      margin-top: 50px;
      display: flex;
      flex-wrap: wrap;
      .x2,
      .g3,
      .two,
      .one {
        width: 172px;
        height: 215px;
        color: #ccc;
        cursor: pointer;
        transition: all 0.5s;
        font-size: 16px;
        &:hover {
          box-shadow: 0 2px 16px rgba(0, 0, 0, 0.2), 0 0 1px rgba(0, 0, 0, 0.2),
            0 0 1px rgba(0, 0, 0, 0.2);
          color: #00a2e9;
          z-index: 1;
        }
        // img {
        //   width: 122px;
        //   height: 142px;
        //   margin: 15px 0 5px;
        // }
      }
      .x2 {
        background: url("~@/assets/img/product/x21.png") no-repeat center;
        &:hover {
          background: url("~@/assets/img/product/x22.png") no-repeat center;
        }
      }
      .g3 {
        background: url("~@/assets/img/product/g31.png") no-repeat center;
        &:hover {
          background: url("~@/assets/img/product/g32.png") no-repeat center;
        }
      }
      .two {
        background: url("~@/assets/img/product/two1.png") no-repeat center;
        &:hover {
          background: url("~@/assets/img/product/two2.png") no-repeat center;
        }
      }
      .one {
        background: url("~@/assets/img/product/one1.png") no-repeat center;
        &:hover {
          background: url("~@/assets/img/product/one2.png") no-repeat center;
        }
      }
    }
  }
